
import React, { Component } from "react";
import { NavBar, InputItem, List, Stepper, Badge, Radio, Button, WhiteSpace, Modal } from "antd-mobile";
import Catalog from './Catalog'
import Summary from './Summary'
import { get, post, put } from "./../utils/fetcher";
import config from "./../config";
import { getFacebookUser, setToken, getToken } from './../appData';

const RadioItem = Radio.RadioItem;

const shippingTypes = [
    { value: '0', text: '직접수령' },
    { value: 'express', text: '택배' },
]

function calculateKo(qtyL, qtyS) {
    const c = Math.min(qtyL, qtyS)
    return c * 17000 + (qtyL - c) * 12000 + (qtyS - c) * 8000
}

function calculateCn(qtyL, qtyS) {
    const c = Math.min(qtyL, qtyS)
    return c * 100 + (qtyL - c) * 70 + (qtyS - c) * 50
}

class Form extends Component {
    state = {
        member: {},
        application: {
            shippingType: '0',
            name: '',
            mobile: '',
            qtyL: 0,
            qtyS: 0,
            priceKo: 0,
            priceCn: 0,
            address: '',
        },
        visible: '',
        // visibleExist: false,
        // visibleConfirm: false,
        // visibleSummary: false,
    }
    componentDidMount = async _ => {
        const user = getFacebookUser()
        if (!user) {
            this.props.history.push('/login')
            return
        }

        let res = await post(`${config().api}/v1/members/login/facebook`, {
            accessToken: user.accessToken,
            data_access_expiration_time: user.data_access_expiration_time,
            email: user.email,
            expiresIn: user.expiresIn,
            name: user.name,
            signedRequest: user.signedRequest,
            userID: user.userID,
        })
        if (res.success === false) {
            return
        }
        if (res.result && res.result.token) {
            setToken(res.result.token)
            this.setState({ member: res.result.member, application: { ...this.state.application, name: res.result.member.name } })
            await this.getExist()
            return
        }
    }

    getExist = async _ => {
        if (!this.state.member.id) {
            return
        }

        const res = await get(`${config().api}/v1/applications?memberId=${this.state.member.id}`)
        if (res.result && res.result.length > 0) {
            this.setState({
                visible: 'exist',
                application: res.result[0],
            })
        }
    }

    save = async _ => {
        const application = { ...this.state.application, memberId: this.state.member.id }
        if (application.id) {
            const res = await put(`${config().api}/v1/applications/${application.id}`, application);
            this.setState({ application: res.result, visible: 'summary' })
            return
        }
        const res = await post(`${config().api}/v1/applications`, application);
        this.setState({ application: res.result, visible: 'summary' })
    }

    render = _ => <div>
        <NavBar
            mode="light"
            leftContent="취소"
            // icon={<Icon type="left" />}
            onLeftClick={_ => { this.props.history.push('/') }}
        >신청하기</NavBar>
        <List renderHeader={() => '가격'}>
            <Catalog />
            <WhiteSpace />
        </List>
        <List renderHeader={() => '구매 정보'}>
            <InputItem
                value={this.state.application.name}
                placeholder="input your name"
                onChange={name => this.setState({ application: { ...this.state.application, name } })}
            >
                이름
            </InputItem>
            <InputItem
                type="phone"
                placeholder="input your phone"
                value={this.state.application.mobile}
                onChange={mobile => this.setState({ application: { ...this.state.application, mobile } })}
            >전화번호</InputItem>
            <List.Item
                wrap
                extra={
                    <Stepper
                        style={{ width: '100%', minWidth: '100px' }}
                        showNumber
                        max={10000}
                        min={0}
                        value={this.state.application.qtyL}
                        onChange={v => this.setState({
                            application: {
                                ...this.state.application,
                                qtyL: v,
                                priceKo: calculateKo(v, this.state.application.qtyS),
                                priceCn: calculateCn(v, this.state.application.qtyS),
                            }
                        })}
                    />}
            >
                큰것
            </List.Item>
            <List.Item
                wrap
                extra={
                    <Stepper
                        style={{ width: '100%', minWidth: '100px' }}
                        showNumber
                        max={10000}
                        min={0}
                        value={this.state.application.qtyS}
                        onChange={v => this.setState({
                            application: {
                                ...this.state.application,
                                qtyS: v,
                                priceKo: calculateKo(this.state.application.qtyL, v),
                                priceCn: calculateCn(this.state.application.qtyL, v),
                            }
                        })}
                    />}
            >
                작은것
            </List.Item>
            <List.Item extra={this.state.application.priceKo}>가격</List.Item>
        </List>
        <List renderHeader={() => '수령방식'}>
            {shippingTypes.map(i => (
                <RadioItem
                    key={i.value}
                    checked={this.state.application.shippingType === i.value}
                    onChange={_ => this.setState({ application: { ...this.state.application, shippingType: i.value } })}
                >
                    {i.text}
                </RadioItem>
            ))}
            {this.state.application.shippingType !== 'express' ? '' :
                <InputItem
                    value={this.state.application.address}
                    placeholder="input your address"
                    onChange={address => this.setState({ application: { ...this.state.application, address } })}
                >
                    주소
            </InputItem>
            }
        </List>
        <WhiteSpace />
        <WhiteSpace />
        <Button type="warning" onClick={_ => this.setState({ visible: 'confirm' })}>구매</Button>
        <Modal
            transparent
            // popup
            // animationType="slide-up"
            visible={this.state.visible === 'exist'}
            title="신청내역"
            footer={[
                {
                    text: '상세보기',
                    onPress: _ => {
                        this.setState({ visible: '' })
                        this.props.history.push('/detail?memberId=' + this.state.member.id)
                    }
                },
                { text: '수정', onPress: _ => this.setState({ visible: '' }) }
            ]}
        >
            <Summary item={this.state.application} />
            <WhiteSpace />
            문의: 123-4567-8900
        </Modal>
        <Modal
            transparent
            // popup
            // animationType="slide-up"
            visible={this.state.visible === 'confirm'}
            title="신청내역 확인"
            footer={[
                { text: '취소', onPress: _ => this.setState({ visible: '' }) },
                { text: '신청', onPress: _ => this.save() }
            ]}
        >
            <Summary item={this.state.application} />
        </Modal>
        <Modal
            closable
            transparent
            // popup
            // animationType="slide-up"
            visible={this.state.visible === 'summary'}
            title="신청이 완료되었습니다"
            onClose={_ => {
                this.setState({ visible: '' })
                this.props.history.push('/detail?memberId=' + this.state.member.id)
            }}
        >
            <Summary item={this.state.application} />
        </Modal>
    </div>
}
export default Form


