import React from "react";
import { InputItem, List } from "antd-mobile";
import Currency from './Currency'

const Summary = ({ item }) => (
    <div>
        <List>
            <List.Item extra={item.name}>이름</List.Item>
            <InputItem editable={false} extra={item.mobile}>전화번호</InputItem>
            <List.Item editable={false} extra={<span>{item.qtyL > 0 ? <span><small>큰것: </small>{item.qtyL} <br /></span> : ''} {item.qtyS > 0 ? <span><small>작은것: </small>{item.qtyS}</span> : ''}</span>}>수량</List.Item>
            <InputItem editable={false} extra={<Currency price={item.priceKo} />}>가격</InputItem>
            <List.Item extra={item.shippingType === 'express' ? '택배' : '직접수령'}>수령방법</List.Item>
            {item.shippingType !== 'express' ? '' : <List.Item wrap><font color="#888" size="2">{item.address}</font></List.Item>}
        </List>
    </div>
)

export default Summary


