
import React, { Component } from "react";
import { NavBar, Icon, Button } from "antd-mobile";
import FacebookLogin from 'react-facebook-login';
import { setFacebookUser, getFacebookUser } from './../appData';

class Login extends Component {
    render = _ => <div>
        <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={_ => { this.props.history.push('/') }}
        >로그인</NavBar>
        <FacebookLogin
            appId="724021918096264"
            autoLoad={true}
            fields="name,email,picture"
            onClick={_ => console.log('click')}
            callback={res => { setFacebookUser(res) }} />
        <Button onClick={_ => console.log(getFacebookUser())}></Button>
    </div>
}
export default Login


