import React, { Component } from "react";
import { NavBar, Icon, WhiteSpace, List, InputItem } from "antd-mobile";
import { get } from "./../utils/fetcher";
import config from "./../config";
import Summary from './Summary'
import wechatQR from './../assets/wechat_qr.jpeg'

class Detail extends Component {
    state = {
        item: {},
    }
    componentDidMount = async _ => {
        const memberId = new URLSearchParams(this.props.location.search).get("memberId")
        if (memberId) {
            const res = await get(`${config().api}/v1/applications?memberId=${memberId}`)
            if (res.result && res.result.length > 0) {
                this.setState({
                    item: res.result[0],
                })
            }
        }
    }
    render = _ => <div>
        <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={_ => { this.props.history.push('/') }}
        >신청내역 상세보기</NavBar>
        <Summary item={this.state.item} />
        <WhiteSpace />
        <List renderHeader={() => '문의'}>
            <InputItem editable={false} extra="123-4567-8900">전화</InputItem>
            <InputItem editable={false} extra="bbugguj@gmail.com">카톡 아이디</InputItem>
            <WhiteSpace />
            12/30까지 배송해드리겠습니다
            <img src={wechatQR} width='100%' />
        </List>

    </div>
}

export default Detail


