const config = _ => {
    switch (process.env.REACT_APP_ENV) {
        case 'production':
            return {
                api: "https://www.artacts.org/i-want-calendar-api"
            }
        default:
            return {
                api: "http://localhost:8000"
            }
    }
}

export default config;


