import React, { Component } from "react";
import { Grid, Carousel, WingBlank, Modal, NavBar, Icon } from 'antd-mobile';
import calendar1 from './../assets/calendar/L1.jpeg'
import calendar1B from './../assets/calendar/L1B.jpeg'
import calendar2 from './../assets/calendar/L2.jpeg'
import calendar2B from './../assets/calendar/L2B.jpeg'
import calendar3 from './../assets/calendar/L3.jpeg'
import calendar3B from './../assets/calendar/L3B.jpeg'
import calendar4 from './../assets/calendar/L4.jpeg'
import calendar4B from './../assets/calendar/L4B.jpeg'
import calendar5 from './../assets/calendar/L5.jpeg'
import calendar5B from './../assets/calendar/L5B.jpeg'
import calendar6 from './../assets/calendar/L6.jpeg'
import calendar6B from './../assets/calendar/L6B.jpeg'
import calendar7 from './../assets/calendar/L7.jpeg'
import calendar7B from './../assets/calendar/L7B.jpeg'
import calendar8 from './../assets/calendar/L8.jpeg'
import calendar8B from './../assets/calendar/L8B.jpeg'
import calendar9 from './../assets/calendar/L9.jpeg'
import calendar9B from './../assets/calendar/L9B.jpeg'
import calendar10 from './../assets/calendar/L10.jpeg'
import calendar10B from './../assets/calendar/L10B.jpeg'
import calendar11 from './../assets/calendar/L11.jpeg'
import calendar11B from './../assets/calendar/L11B.jpeg'
import calendar12 from './../assets/calendar/L12.jpeg'
import calendar12B from './../assets/calendar/L12B.jpeg'

const data = [
    { id: 1, icon: calendar1, text: "1월 앞면" },
    { id: 2, icon: calendar1B, text: "1월 뒷면" },
    { id: 3, icon: calendar2, text: "2월 앞면" },
    { id: 4, icon: calendar2B, text: "2월 뒷면" },
    { id: 5, icon: calendar3, text: "3월 앞면" },
    { id: 6, icon: calendar3B, text: "3월 뒷면" },
    { id: 7, icon: calendar4, text: "4월 앞면" },
    { id: 8, icon: calendar4B, text: "4월 뒷면" },
    { id: 9, icon: calendar5, text: "5월 앞면" },
    { id: 10, icon: calendar5B, text: "5월 뒷면" },
    { id: 11, icon: calendar6, text: "6월 앞면" },
    { id: 12, icon: calendar6B, text: "6월 뒷면" },
    { id: 13, icon: calendar7, text: "7월 앞면" },
    { id: 14, icon: calendar7B, text: "7월 뒷면" },
    { id: 15, icon: calendar8, text: "8월 앞면" },
    { id: 16, icon: calendar8B, text: "8월 뒷면" },
    { id: 17, icon: calendar9, text: "9월 앞면" },
    { id: 18, icon: calendar9B, text: "9월 뒷면" },
    { id: 19, icon: calendar10, text: "10월 앞면" },
    { id: 20, icon: calendar10B, text: "10월 뒷면" },
    { id: 21, icon: calendar11, text: "11월 앞면" },
    { id: 22, icon: calendar11B, text: "11월 뒷면" },
    { id: 23, icon: calendar12, text: "12월 앞면" },
    { id: 24, icon: calendar12B, text: "12월 뒷면" },
]


const CalendarItem = () => (
    <WingBlank>
        <Carousel
            autoplay={false}
            infinite
            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
            afterChange={index => console.log('slide to', index)}
        >
            {data.map(item => (
                <img
                    key={item.id}
                    src={item.icon}
                    alt=""
                    style={{ width: '100%', verticalAlign: 'top' }}
                />
            ))}
        </Carousel>
    </WingBlank>
)

class Calendar extends Component {
    state = {
        visible: false,
        currentItem: {},
    }
    render = _ => <div>
        <NavBar
            mode="light"
            rightContent={[
                <span key={1} onClick={_=> this.props.history.push('/request')}>신청하기</span>
            ]}
        >2019 Calendar</NavBar>
        {/* <CalendarItem /> */}
        <div>
        <Grid
            data={data}
            columnNum={2}
            square={false}
            hasLine={false}
            itemStyle={{padding: 1}}
            onClick={e => { this.setState({ visible: true, currentItem: e }); console.log(e); }}
            renderItem={dataItem => (
                <div >
                    <img src={dataItem.icon} style={{ width: '100%' }} alt="" />
                    <div style={{ color: '#888', fontSize: '13px'}}>
                        <span>{dataItem.text}</span>
                    </div>
                </div>
            )}
        />
        </div>
        <Modal
            transparent
            visible={this.state.visible}
            closable
            onClose={_ => this.setState({ visible: false })}
        // animationType="slide-up"
        // afterClose={() => { console.log('afterClose'); }}
        >
            <img
                key={this.state.currentItem.id}
                src={this.state.currentItem.icon}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
            />
            {/* <CalendarItem /> */}
        </Modal>
    </div>
}

export default Calendar


