import React from "react";
import NumberFormat from "react-number-format";

const Currency = ({ price }) => (
    <NumberFormat
        value={price}
        displayType={"text"}
        thousandSeparator={true}
        prefix={"₩"}
        decimalScale={0}
        fixedDecimalScale={true}
    />
)

export default Currency


